/* eslint-disable eqeqeq */
import React, { useEffect, useMemo, useState } from "react";
import { Button, Container, Paper, Typography } from "@mui/material";
import { Link, useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  getByIdService,
  setByIdServiceDetail,
  setIsDetailLoading,
} from "../../../redux/reducers/pay24";
import Balance from "../../../components/Balance";
import CabinetTop from "../../../components/cabinetTop";
import SkeletonPayments from "./Skeleton";
import { Box } from "@mui/system";
import { makeStyles } from "@mui/styles";
import ErrorOutlineIcon from "@mui/icons-material/ErrorOutline";
import { Grid, IconButton, Tooltip } from "@mui/material";

const Services = ({ t, main, setVerificationPush, mode }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const { servicesId } = useParams();

  const [country, setCountry] = useState("");
  const [open, setOpen] = useState(false);
  const [allCountries, setCountries] = useState([]);

  const handleTooltipClose = () => {
    setOpen(false);
  };

  const handleTooltipOpen = (name) => {
    setOpen(name);
  };

  useEffect(() => {
    dispatch(getByIdService(servicesId));
    dispatch(setIsDetailLoading(true));

    // eslint-disable-next-line
  }, []);

  const { Services, isLoadingServices } = useSelector(
    ({ Pay24Reducer }) => Pay24Reducer
  );

  const { profileData } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );

  const account = profileData?.data?.profile?.verification?.value;

  const [filter, setFilter] = useState("");

  const filteredCoin = useMemo(
    () =>
      Services.filter((item) =>
        item.name
          .toLocaleLowerCase()
          .startsWith(filter.toLocaleLowerCase().trim())
      ),
    // eslint-disable-next-line
    [filter]
  );

  const ServicesList = filter ? filteredCoin : Services;

  useEffect(() => {
    dispatch(setByIdServiceDetail({}));
  }, []);


  useEffect(() => {
    const countries = Services.map(
      (item) => JSON.parse(item.data).provider["@country"]
    );

    setCountries(
      countries?.filter((item, pos) => countries.indexOf(item) == pos)
    );
  }, [Services]);

  useEffect(() => {
    if (allCountries.includes("Кыргызстан")) setCountry("Кыргызстан");
    else setCountry(allCountries[0]);
  }, [allCountries]);

  useEffect(() => {
    if (main) document.title = "Binance Pay";
  }, []);

  return (
    <Paper
      component="section"
      sx={{
        minHeight: "85vh",
        pt: main && 4,
        pb: main && 4,
      }}
    >
      <Container maxWidth="lg">
        {!main && <Balance />}

        <CabinetTop
          payment
          title={t("payments")}
          setFilter={setFilter}
          input
          placeHolder="Поиск услуг..."
        />

        <Box
          display="flex"
          flexDirection={{ xs: "column", lg: "row" }}
          alignItems="start"
        >
          <Paper
            sx={{
              background: mode === "light" && "#fff",
              boxShadow: mode === "light" && " 0px 1px 0px #E2E8F0",
              borderRadius: "13px",
              display: "flex",
              flexDirection: "column",
              padding: "24px",
              width: { xs: "100%", md: "30%" },
              rowGap: "16px",
              mr: { xs: 0, lg: 2 },
              mb: { xs: 2, lg: 0 },
            }}
          >
            {allCountries.includes("Кыргызстан") && (
              <Button
                variant="contained"
                className={
                  country === "Кыргызстан"
                    ? classes.button_active
                    : classes.button
                }
                size="large"
                onClick={() => setCountry("Кыргызстан")}
              >
                Кыргызстан
              </Button>
            )}

            {allCountries
              ?.sort()
              .reverse()
              .filter((item) =>
                allCountries.includes("Кыргызстан")
                  ? item !== "Кыргызстан"
                  : item
              )
              .map((item, idx) => (
                <Button
                  key={idx}
                  variant="contained"
                  className={
                    country === item ? classes.button_active : classes.button
                  }
                  size="large"
                  onClick={() => setCountry(item)}
                >
                  {item}
                </Button>
              ))
              .reverse()}
          </Paper>
          <Grid container spacing={2}>
            {isLoadingServices
              ? Array.from(Array(20).keys()).map((item, idx) => (
                  <Grid item key={idx} xs={12} sm={6} md={4} lg={4} xl={4}>
                    <SkeletonPayments />
                  </Grid>
                ))
              : Services &&
                ServicesList.filter(
                  (item) =>
                    JSON.parse(item.data).provider["@country"] === country &&
                    item.id !== 644
                ).map((item, idx) => (
                  <Grid key={idx} item xs={12} sm={6} md={4} lg={4} xl={4}>
                    {!main && account != 2 ? (
                      <Box
                        style={{
                          background: mode === "light" && "#FFF",
                          boxShadow: mode === "light" && "0px 1px 0px #E2E8F0",
                        }}
                        sx={{ position: "relative" }}
                        onClick={() => setVerificationPush(true)}
                      >
                        <div className={classes.box}>
                          <img src={item.url_logo} alt="" />
                          <Typography className={classes.title}>
                            {item.name}
                          </Typography>
                        </div>
                      </Box>
                    ) : item.active === "1" ? (
                      <Box sx={{ position: "relative" }}>
                        <div
                          style={{
                            background: mode === "light" && "#FFF",
                            boxShadow:
                              mode === "light" && "0px 1px 0px #E2E8F0",
                          }}
                          className={`${classes.box} disabled`}
                        >
                          <img src={item.url_logo} alt="" />
                          <Typography className={classes.title}>
                            {item.name}
                          </Typography>
                        </div>
                        <div>
                          <Tooltip
                            PopperProps={{
                              disablePortal: true,
                            }}
                            arrow
                            sx={{ maxWidth: "unset" }}
                            title={t("unavailable")}
                            placement="top-start"
                            onClose={handleTooltipClose}
                            open={open === item?.name}
                            disableFocusListener
                            disableHoverListener
                            disableTouchListener
                          >
                            <IconButton
                              sx={{
                                position: "absolute!important",
                                bottom: 8,
                                right: 8,
                                color: "#777",
                                fontSize: "12px",
                                fontWeight: "600",
                                display: "flex",
                                alignItems: "center",
                              }}
                              onClick={() => handleTooltipOpen(item?.name)}
                            >
                              <ErrorOutlineIcon />
                            </IconButton>
                          </Tooltip>
                        </div>
                      </Box>
                    ) : (
                      <Link
                        style={{
                          background: mode === "light" && "#FFF",
                          boxShadow: mode === "light" && "0px 1px 0px #E2E8F0",
                        }}
                        className={classes.box}
                        to={
                          !main
                            ? `/dashboard/payment-for-services/${servicesId}/${item.api_id}`
                            : `/main/payment-for-services/binance-pay/${servicesId}/${item.api_id}`
                        }
                      >
                        <img src={item.url_logo} alt="" />
                        <Typography className={classes.title}>
                          {item.name}
                        </Typography>
                      </Link>
                    )}
                  </Grid>
                ))}
          </Grid>
        </Box>
      </Container>
    </Paper>
  );
};

export default Services;

const useStyles = makeStyles((theme) => ({
  box: {
    position: "relative",
    display: "flex",

    height: "104px",
    padding: "8px 8px 8px 16px",
    justifyContent: "flex-start",
    alignItems: "center",
    overflow: "hidden",
    transition: "all 0.2s linear",
    borderRadius: "8px",
    "&:hover": {
      boxShadow: "0px 0px 6px 2px #E2E8F0",
    },

    "&.disabled": {
      cursor: "not-allowed",
      opacity: 0.4,
      boxShadow: "0px 1px 0px #E2E8F0!important",
    },

    "& img": {
      width: "40px",
      height: "40px",
      objectFit: "contain",
      marginRight: 16,
    },
  },

  title: {
    fontWeight: "500",
    fontSize: "18px",
    lineHeight: "23px",
    letterSpacing: "-0.02em",
  },

  button: {
    width: "100%",
    background: "transparent",
    color: "#B8C0CC",

    "&:hover": {
      "& .MuiButton-label": {
        color: "#000",
      },
    },
  },

  button_active: {
    width: "100%",
    background: "#FCE34D",
    color: "#000",
  },
}));
